import {
    TableBody,
    TableCell,
    TableRow,
  } from "@windmill/react-ui";
  import MainDrawer from "components/Properties/EditPropertiesModal";
  import ProductDrawer from "components/activities/EditActivity";
  import DeleteModal from "components/activities/DeleteActivities";
  import ShowHideButton from "components/table/ShowHideButton";
  import Tooltip from "components/tooltip/Tooltip";
  import { SidebarContext } from "context/SidebarContext";
  import { useContext, useState } from "react";
  import useToggleDrawer from "hooks/useToggleDrawer";
  import { t } from "i18next";
  import { FiEdit, FiTrash2 } from "react-icons/fi";
import { getActivitiesById } from "services/Api";
  
  //internal import
  
  const ProductTable = ({products,isCheck,property,setproperty,propertyList,getActivityList, setIsCheck,wholeCount, pagenumber }) => {
    const {toggleModal ,toggleDrawer1,toggleDrawer,setIsDrawerOpen1} =
    useContext(SidebarContext);
    // const { title, serviceId } = useToggleDrawer();
    const [serviceId, setServiceId] = useState(1)
    const [title, settitle] = useState("")
    const handleClick = (e) => {
      const { id, checked } = e.target;
      console.log("id", id, checked);
  
      setIsCheck([...isCheck, id]);
      if (!checked) {
        setIsCheck(isCheck.filter((item) => item !== id));
      }
    };
  
    const handleUpdate = async(e) => {
      console.log(e.id)
      const singleview = await getActivitiesById(e.id);
      if (singleview && singleview.status === 200) {
        console.log(singleview?.data?.data?.activity,"singleview")
        setproperty(singleview?.data?.data?.activity)
      }
      toggleDrawer1();
        }
        const handleModalOpen = (data) => {
          console.log(data,"DELETEsww","ww")
          setServiceId(data?.id);
          settitle(data?.name)
          setproperty(data)
          toggleModal();
          // setTitle(title);
        };
    return (
      <>
        {isCheck?.length < 1 && <DeleteModal property={property} id={serviceId} getActivityList={getActivityList} title={title} />}
        <MainDrawer>
        <ProductDrawer pagenumber={pagenumber} isOpen={toggleDrawer1} onClose={toggleDrawer1} getActivityList={getActivityList} propertyList={propertyList}  property={property}/>
        </MainDrawer> 
        <TableBody>
          {products?.map((properties, i) => (
            <TableRow key={i + 1}>
              <TableCell>
            <span className="text-sm">{(wholeCount?.currentPage-1)*wholeCount?.pageSize+i+1}</span>
              </TableCell>   
              <TableCell className="textcorrect">
                <span className="text-sm">{properties?.name}</span>
              </TableCell>        
              <TableCell className="textcorrect">

              <ul>{properties?.Activity_properties?.map((pro, i) => ( <li className="text-sm ">{pro?.Property?.name}</li> ))}</ul>
              </TableCell>
              <TableCell className="textcorrect">
                <span className="text-sm">{properties?.category}</span>
              </TableCell>
              <TableCell className="textcorrect">
               {properties?.category ==='paid' ? <div><span className="text-sm">{"AMOUNT : "+ properties?.price}</span>, <br/><span className="text-sm">{"CGST : "+ properties?.cgst}</span>,<br/> <span className="text-sm">{"SGST : "+ properties?.sgst}</span> </div> : 'Its Complementry Activity'}
              </TableCell>
              <TableCell>
              {properties?.Activity_images?.map((img) => (<img
              className="object-cover imgCustom"
              src={img.image}
              alt="Office"
            />))}
              </TableCell>
              {/* <TableCell className="text-center">
                <ShowHideButton id={properties._id} status={properties.status} />
              </TableCell> */}
              <TableCell>
                {/* <EditDeleteButton
                  id={properties._id}
                  product={properties}
                  isCheck={isCheck}
                  handleUpdate={handleUpdate}
                  handleModalOpen={handleModalOpen}
                  title={showingTranslateValue(properties?.title, lang)}
                /> */}
                 <button
            onClick={() => {
              handleUpdate(properties)
            }}
            className="p-2 cursor-pointer text-gray-400 hover:text-green-600 focus:outline-none"
          >
            <Tooltip
              id="edit"
              Icon={FiEdit}
              title={t("Edit")}
              bgColor="#10B981"
            />
          </button>
        <button
          onClick={() => handleModalOpen(properties)}
          className="p-2 cursor-pointer text-gray-400 hover:text-red-600 focus:outline-none"
        >
          <Tooltip
            id="delete"
            Icon={FiTrash2}
            title={t("Delete")}
            bgColor="#EF4444"
          />
        </button>
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </>
    );
  };
  
  export default ProductTable;
  